import React from "react";
import styled from "styled-components";
import flechaRegresar from "../assets/icons/flechaRegresar.svg";

// rsc

const BtnRegresar = (props) => {
  return (
    <ButtonRegresar onClick={props.regresar}>
      <img src={flechaRegresar} alt="icono flecha regresar" />
      <span>Regresar</span>
    </ButtonRegresar>
  );
};

export default BtnRegresar;

// ESTILOS

const ButtonRegresar = styled.button`
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 20px 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  span {
    padding-left: 10px;
  }
`;

